<script>
  import axios from 'axios'
  var formatThousands = require('format-thousands')

  export default {
    data: () => ({
      tab: null,
      formatThousands,
      date: [],
      dialog: false,
      menu: false,
      dialog2: false,
      dialogDelete: false,
      balanceDialog: false,
      balanceDialog2: false,
      wallet_id: [],
      amount: 0,
      amount2: 0,
      credit: 0,
      debit: 0,
      begin_balance: 0,
      ending_balance: 0,
      sortable: false,
      search: '',
      headers: [
        {
          text: 'No.',
          value: 'snow',
          width: '50px',
        },
        {
          text: 'Date',
          value: 'wallet_date',
        },
        {
          text: 'Driver List',
          value: 'driver_name',
        },
        {
          text: 'Status',
          value: 'status',
        },
        {
          text: 'Balance',
          value: 'ending_balance',
        },
        {
          text: 'Actions',
          value: 'actions',
        },
      ],
      headers2: [
        {
          text: 'No.',
          value: 'snow',
        },
        {
          text: 'Driver List',
          value: 'driver_name',
        },
        {
          text: 'Balance',
          value: 'ending_balance',
        },
        {
          text: 'Actions',
          value: 'actions',
        },
      ],
      headers3: [
        {
          text: 'Tanggal',
          value: 'date',
        },
        {
          text: 'Deskripsi',
          value: 'description',
        },
        {
          text: 'Jenis Transaksi',
          value: 'type',
        },
        {
          text: 'Nominal',
          value: 'amount',
        },
      ],

      detail: [],
      detail2: [],
      express: [],
      reguler: [],
      editedIndex: -1,
      detailItem: {},
    }),

    computed: {
      expressWithNo () {
        return this.express.map((d, index) => ({ ...d, snow: index + 1 }))
      },
      regulerWithNo () {
        return this.reguler.map((d, index) => ({ ...d, snow: index + 1 }))
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
      this.getDataExpress()
    },

    methods: {
      initialize () {
        axios.get('admin/driver/wallet').then(res => {
          this.reguler = res.data.data
        })
      },
      getDataExpress () {
        axios.get('admin/driver/wallet-exp')
          .then((res) => {
            this.express = res.data.data
          })
      },

      det_wallet_reg (item) {
        const id = item.item.wallet_id
        axios.get('admin/driver/wallet/' + id).then(res => {
          this.detail2 = res.data.data
          this.begin_balance = res.data.begin_balance
          this.ending_balance = res.data.ending_balance
          this.credit = res.data.credit
          this.debit = res.data.debit
          this.dialog = true
        })
      },

      det_wallet_exp (item) {
        const id = item.item.wallet_id
        axios.get('admin/driver/wallet/' + id).then(res => {
          this.detail2 = res.data.data
          this.begin_balance = res.data.begin_balance
          this.ending_balance = res.data.ending_balance
          this.credit = res.data.credit
          this.debit = res.data.debit
          this.dialog2 = true
        })
      },

      deleteItem (item) {
        this.editedIndex = this.wallet.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.wallet.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.wallet[this.editedIndex], this.editedItem)
        } else {
          this.wallet.push(this.editedItem)
        }
        this.close()
      },

      addBalance () {
        axios.post('admin/driver/add-balance', {
          id: this.wallet_id,
          amount: this.amount,
        })
          .then(() => {
            this.$swal('Data Updated Successfully', '', 'success')
            this.balanceDialog = false
            this.beginBalance = 0
            this.wallet_id = null
            this.selected = []
            this.initialize()
          })
      },

      addBalance2 () {
        axios.post('admin/driver-exp/add-balance', {
          id: this.wallet_id,
          begin_balance: this.amount2,
        })
          .then(() => {
            this.$swal('Data Updated Successfully', '', 'success')
            this.balanceDialog2 = false
            this.begin_balance = 0
            this.wallet_id = null
            this.selected = []
            this.initialize()
          })
      },

      takeBalance (item) {
        this.$swal({
          title: 'Apakah anda yakin ingin menarik saldo?',
          showCancelButton: true,
          confirmButtonText: 'Yes',
        }).then((result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios.get('admin/driver/pull-balance/' + item.item.wallet_id).then(() => {
              this.$swal('Transaction Success', '', 'success')
              this.initialize()
              this.getDataExpress()
            }).catch(function (error) {
              console.log(error.response)
            })
          }
        })
      },

      dateFilterReguler (roleId) {
        axios.post('admin/driver/wallet/', {
          from: this.date[0],
          to: this.date[1],
          role: roleId,
        }).then((res) => {
          this.reguler = res.data.data
        })
      },

    },
  }
</script>
